.reg{
    background-color:#165366;
    border: 1px solid black;
    width:15%;
    border-radius:2%;
    box-shadow: 1px 1px 20px black;
    margin:10% auto auto auto ;

    text-align:center;

    padding-bottom:5vh;
    padding-top:5vh;
    animation-name:regist;
    animation-duration:2s;
}

input{
    width:10vw;
    height:3vh;
    border-radius:5px;
}
.reg p{
    margin-bottom:0%;
    color:white;
}

.butt{
    color:#57faea;
    background-color: #165366 ;
    height:2vw;
    border-radius: 5px;
    margin:10% auto auto auto;
    box-shadow: 1px 1px 10px black;
}
.back{
    background-color:#109ac4;
    height:100vh;
    width:100vw;
}

@keyframes regist {
   from{
    opacity:0;
    transform: translate(275%,-75%) scale(0.2) ;
   }
   to{
       opacity:1;
       transform: scale(1);
   }
 
}